ntre Europa y Asia. Hoy se conoce como Estambul. Pero en el año 395, el emperador Teodosio dividió el imperio, separando el mundo latino del griego. Luego de enfrentar la invasión germánica, solamente las provincias de oriente continuaron el legado cultural romano, fundiéndose con la cultura helenística. En ese momento, el imperio era teocrático, con un solo Dios y una sola autoridad, el emperador, quien estaba encargado de defender y propagar la fe; sin embargo, la división del imperio produjo también la división religiosa: en oriente, eran ortodoxos y en occidente, cristianos. Esta situación produjo también cambios en el arte, debido a su ubicación privilegiada entre Europa y Oriente.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/29.jpg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("El arte bizantino")]),_c('p',[_vm._v("La pintura en este periodo representa el absoluto influjo de la religión en la vida social y política del Imperio Oriental, pero a comienzos del año 730, bajo la autoridad del emperador León III, surgió una crisis iconoclasta que duró hasta el siglo IX, cuando se prohíbe la elaboración de imágenes religiosas en el Imperio, ya que se consideró que dichas imágenes conducían a la idolatría, porque en la materia estaba lo maligno y, por lo tanto, no contribuían en el propósito de seguir el camino a lo divino. Por esa razón, los mosaicos fueron cubiertos dentro de los templos, los motivos religiosos se reemplazaron por retratos del emperador, escenas de guerra, de carreras de caballos o de caza, vegetales o animales."),_c('br'),_c('br'),_vm._v("Luego vino el cisma entre las iglesias de Constantinopla y Roma, entonces las imágenes volvieron a ser admitidas en los templos, pero con un valor sagrado, rindiendo culto a Dios por medio de ellas.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/30.jpg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("El arte románico")]),_c('p',[_vm._v("Surge en el siglo X y se extiende hasta el XII. En el siglo X, el emperador Otón, junto con Carlomagno, quien gobernó en el siglo IX, fijó las bases de la Europa feudal, caracterizada por el feudo y la dependencia de los vasallos respecto a su señor. En el centro de la propiedad, que era una enorme extensión de tierra, estaba el castillo, la catedral o la abadía. El poder dependía de la cantidad de tierras que se poseyera, y en la cúspide social estaba el rey. En esa época, la iglesia católica logró la unidad de Europa; pero la Iglesia estaba manejada por el poder civil, pues los obispos eran nobles a quienes los reyes u otros nobles les entregaban sus diócesis; en correspondencia, la institución del papado se fortaleció. Los monjes se dedicaron a realizar trabajos manuales, tareas agrícolas y artesanales, convirtiéndose los monasterios benedictinos en centros económicos independientes y en focos culturales de primera importancia, ya que copiaban manuscritos antiguos, realizaban pinturas de miniaturas y redactaban crónicas históricas. ")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/31.jpg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("El arte románico")]),_c('p',[_vm._v("En cuanto a la arquitectura románica, se caracteriza por las iglesias en forma de cruz latina; usualmente, tienen una cripta en la que se depositaban los cuerpos de los santos. En esta época, se retomaron materiales y técnicas de construcción de los romanos, también se construyeron campanarios. La arquitectura tenía un significado religioso, por eso la escultura románica es monumental, y estas obras, como iban dirigidas a los fieles y los peregrinos, se ubicaban en las portadas de las iglesias, dentro de ellas y en los conventos. Las imágenes tienen como protagonista a Cristo. Utilizaron la decoración geométrica, vegetal, humana o animal, dispuesta en sentido radial. En las columnas también esculpieron estatuas; las representaciones, en general, eran religiosas, relacionadas con diferentes pasajes de la Biblia, por ejemplo, los cuatro evangelistas, el juicio final y otros. La ubicación de las imágenes dentro de las iglesias se hacía por orden jerárquico y de importancia, por ejemplo, en el norte está el antiguo testamento, el sur es el nuevo y el oeste el juicio final. Las pinturas murales se ubicaron en el ábside o parte posterior del altar. ")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/32.jpg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("El arte gótico")]),_c('p',[_vm._v("Se inició en el siglo XII, alcanzando su esplendor en los siglos XIII al XV. Los cambios en la sociedad produjeron el desplazamiento de los campos hacia las regiones urbanas, donde dominó la burguesía, clase fundamentalmente comerciante, que propendió por el sentido comunal, la defensa de los intereses de sus habitantes y la autonomía municipal. Por esa razón, al lado del poder religioso representado en las catedrales, se construyeron los ayuntamientos. Los artesanos y los artistas se organizaron en gremios que buscaban evitar la competencia y el monopolio. Los gremios se encargaron de realizar numerosas obras artísticas, siendo la ciudad un polo de desarrollo cultural, sobre todo por el surgimiento de las universidades, que desplazaron a los monasterios. en la arquitectura gótica se utilizó el vidrio con tonos preferencialmente azules y rojos, y la piedra en forma de sillares, por cuanto lo estructural tuvo para la época enorme valor estético.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/33.jpg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("El arte gótico")]),_c('p',[_vm._v("Hasta el siglo XIV en Italia y en el siglo XV en Flandes, la pintura se desarrolló más tarde que la arquitectura y la escultura. En Italia, la pintura tenía influencia bizantina, manteniendo un estilo idealista, con curvas delicadas, fondos dorados y cuerpos alargados, conservando la mayor parte de los descubrimientos helenísticos que pasaron a la edad media. Pero las demandas de la burguesía adinerada exigían más obras pictóricas realistas, en las que se reprodujera el mundo visible aplicando el naturalismo de los escultores góticos. Pintaron especialmente figuras en un espacio que representara profundidad. Giotto utilizó el fresco con dominio completo, introdujo la tercera dimensión, lo que consiguió a través del escorzo y el juego entre luces y sombras, y el fondo antes dorado lo sustituyó por el cielo y el paisaje. Giotto se hizo famoso, hecho que era nuevo, pues hasta el momento las obras de arte eran anónimas, y se convirtió en el precedente de la perspectiva, que fue la gran conquista del Renacimiento. ")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/34.jpg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("El arte gótico")]),_c('p',[_vm._v("En cuanto a Flandes, se produjo una renovación en la pintura, inclinándose hacia la realidad partiendo de la observación, incorporando todos los detalles posibles, utilizando el claroscuro, lo cual hizo ganar en profundidad y volumen a las obras, dando mayor apariencia de vida. Utilizaron el óleo, cubierto al final con una capa de barniz. Esta técnica pictórica permite desarrollar el estilo flamenco, pintando imágenes religiosas 